import { Injectable } from "@angular/core";
import * as io from "socket.io-client";
import hmacSHA256 from "crypto-js/hmac-sha256";
import base64 from "crypto-js/enc-base64";
import Utf8 from "crypto-js/enc-utf8";
var Base64;
@Injectable({
  providedIn: "root",
})
export class SocketService {
  socket: any;

  constructor() {
    const headerObj = {
      alg: "HS256",
      typ: "JWT",
    };
    const payloadObj = { token: "g1ps" };
    const headerObjString = Utf8.parse(JSON.stringify(headerObj));
    const payloadObjString = Utf8.parse(JSON.stringify(payloadObj));

    const encodedHeader = this.base64url(headerObjString);
    const encodedPayload = this.base64url(payloadObjString);

    const token = encodedHeader + "." + encodedPayload;
    const signature = this.base64url(hmacSHA256(token, "baI5"));

    const signedToken = token + "." + signature;

    this.socket = io("http://varunav.in:3000");
    this.socket.on("connect", () => {
      this.socket
        .emit("authenticate", { token: signedToken }) //send the jwt
        .on("authenticated", () => {
          //do other things
        })
        .on("unauthorized", (msg) => {
          throw new Error(msg.data.type);
        });
    });
  }

  getSocket() {
    return this.socket;
  }

  base64url(source) {
    // Encode in classical base64
    let encodedSource = base64.stringify(source);

    // Remove padding equal characters
    encodedSource = encodedSource.replace(/=+$/, "");

    // Replace characters according to base64url specifications
    encodedSource = encodedSource.replace(/\+/g, "-");
    encodedSource = encodedSource.replace(/\//g, "_");

    return encodedSource;
  }

  recieveData(namespace: string) {
    this.socket.on(namespace, (data) => {
      return data;
    });
  }

  sendData(namespace: string, data: any) {
    this.socket.emit(namespace, data);
  }
}
