import {
  Component,
  OnInit,
  Input,
  AfterViewInit,
  ViewChild,
  ElementRef,
  Output,
  AfterContentInit,
  EventEmitter,
  ViewChildren,
} from "@angular/core";
import { SocketService } from "../socket.service";
import {
  trigger,
  transition,
  animate,
  style,
  state,
  query,
  stagger,
  keyframes,
} from "@angular/animations";

@Component({
  selector: "app-card",
  templateUrl: "./card.component.html",
  styleUrls: ["./card.component.css"],
  animations: [
    trigger("stagger", [
      transition("* => *", [
        query(":enter", style({ opacity: 0 }), { optional: true }),
        query(
          ":leave",
          stagger("300ms", [
            animate(
              "500ms ease-out",
              keyframes([
                style({ opacity: 1, transform: "scale(1.05)", offset: 0 }),
                style({ opacity: 0.5, transform: "scale(.5)", offset: 0.5 }),
                style({ opacity: 0, transform: "scale(0)", offset: 1 }),
              ])
            ),
          ]),
          { optional: true }
        ),
        query(
          ":enter",
          stagger("300ms", [
            animate(
              ".5s ease-in",
              keyframes([
                style({ opacity: 0, transform: "translateY(-20%)", offset: 0 }),
                style({
                  opacity: 0.5,
                  transform: "translateY(-5px) scale(1.05)",
                  offset: 0.5,
                }),
                style({
                  opacity: 1,
                  transform: "translateY(0) scale(1)",
                  offset: 1,
                }),
              ])
            ),
          ]),
          { optional: true }
        ),
      ]),
    ]),
  ],
})
export class CardComponent implements OnInit {
  @Input()
  client: any;
  @Input()
  playerObj: any;
  @ViewChildren("cardElement") cardElement: ElementRef;
  @Output()
  cardEl: EventEmitter<ElementRef> = new EventEmitter();
  @ViewChildren("userCardElement") userCardElement: ElementRef;
  @Output()
  userCardEl: EventEmitter<ElementRef> = new EventEmitter();

  constructor(private socketService: SocketService) {}

  socket: any;
  cards: any;
  player: any;
  select: any;
  clientObj: any;
  nSelect: any = 1;
  nSelectedCards: any = 0;
  selectedCards: any = [];
  timer: any;

  ngOnInit(): void {
    this.socket = this.socketService.getSocket();
    this.socket.on("gameroom", (data: any) => {
      if (
        !data.lost
          .map((e) => {
            return e.name;
          })
          .includes(this.client)
      ) {
        this.clientObj = data.playersDict.filter((e) => {
          return e.name == this.client;
        })[0];
      } else {
        this.clientObj = data.lost.filter((e) => {
          return e.name == this.client;
        })[0];
      }
      this.select = this.clientObj.select;
      this.nSelect = this.clientObj.nSelect;
      this.cards = this.clientObj.cards;
    });
    this.player = this.playerObj.name;
    this.cards = this.playerObj.cards;

    // this.cardEl.emit(this.cardElement);
    // this.userCardEl.emit(this.userCardElement);
  }

  selectedCard(card) {
    if (this.select) {
      this.nSelectedCards += 1;
      this.selectedCards.push(card);
      if (this.nSelectedCards == this.nSelect) {
        this.socket.emit("selected", {
          selectedCards: this.selectedCards,
          client: this.client,
        });
        this.nSelectedCards = 0;
        this.selectedCards = [];
      }
    }
  }
}
