import { state, style } from "@angular/animations";

export let translateYwithOpacity = (stateAlias, dy, opacity) => {
  return state(
    stateAlias,
    style({ transform: "translateY(" + dy + ")", opacity: opacity })
  );
};
export let fadeIn = (stateAlias, opacity) => {
  return state(stateAlias, style({ opacity: opacity }));
};
