import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  AfterViewInit,
} from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { SocketService } from "./socket.service";
import {
  trigger,
  transition,
  animate,
  style,
  state,
  query,
  stagger,
  keyframes,
} from "@angular/animations";
import { translateYwithOpacity, fadeIn } from "./animation";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.css"],
  animations: [
    trigger("fade", [
      fadeIn("void", 0),
      fadeIn("*", 1),
      transition("void <=> *", [animate("0.5s 1s ease-in")]),
    ]),
    trigger("moveIn", [
      translateYwithOpacity("void", 0, 0),
      translateYwithOpacity("*", "-50%", 1),
      transition("void <=> *", [animate("0.5s ease-in")]),
    ]),
    trigger("stagger", [
      transition("void <=> *", [
        query(
          ":leave",
          [stagger(100, [animate("0.5s", style({ opacity: 0 }))])],
          { optional: true }
        ),
        query(
          ":enter",
          [
            style({ opacity: 0 }),
            stagger(100, [animate("0.5s 1s", style({ opacity: 1 }))]),
          ],
          { optional: true }
        ),
      ]),
    ]),
    trigger("beat", [
      state("void", style({ opacity: 1 })),
      state("*", style({ opacity: 1 })),
      transition("void <=> *", [
        animate(
          "1s ease-in-out",
          keyframes([
            style({ transform: "scale(1)" }),
            style({ transform: "scale(1.1)" }),
            style({ transform: "scale(1)" }),
          ])
        ),
      ]),
    ]),
  ],
})
export class AppComponent implements OnInit {
  @ViewChild("splash") splash: ElementRef;

  title = "coup";
  players: any;
  client: any;
  socket: any;
  isAdmin: boolean = false;
  loggedIn: boolean = false;
  beat: boolean = true;

  constructor(private http: HttpClient, private socketService: SocketService) {}

  ngOnInit() {
    this.socket = this.socketService.getSocket();
    this.socket.on("players", (data: any) => {
      this.players = data.playersDict.map((e) => {
        return e.name;
      });
    });
  }

  createRoom(admin: any, adminKey: any) {
    this.isAdmin = true;
    this.socket.emit(
      "createRoom",
      { admin: admin, key: adminKey },
      (data: any) => {
        if (data.stat) {
          this.isAdmin = true;
          if (admin == "v3") this.client = "varun";
          this.loggedIn = true;
        } else alert(data.message);
      }
    );
    return false;
  }
  joinRoom(name: any, key: any) {
    this.socket.emit("joinRoom", { name: name, key: key }, (data: any) => {
      if (data.stat) {
        this.client = name;
        this.loggedIn = true;
      } else alert(data.message);
    });
    return false;
  }
}
