import { Component, OnInit, Input } from "@angular/core";
import { SocketService } from "../socket.service";
import {
  trigger,
  transition,
  animate,
  style,
  state,
  query,
  stagger,
  keyframes,
} from "@angular/animations";

@Component({
  selector: "app-room",
  templateUrl: "./room.component.html",
  styleUrls: ["./room.component.css"],
  animations: [
    trigger("stagger", [
      transition("* => *", [
        query(":enter", style({ opacity: 0 }), { optional: true }),
        query(
          ":leave",
          stagger("300ms", [
            animate(
              "500ms ease-out",
              keyframes([
                style({ opacity: 1, transform: "scale(1.05)", offset: 0 }),
                style({ opacity: 0.5, transform: "scale(.5)", offset: 0.5 }),
                style({ opacity: 0, transform: "scale(0)", offset: 1 }),
              ])
            ),
          ]),
          { optional: true }
        ),
        query(
          ":enter",
          stagger("300ms", [
            animate(
              ".5s ease-in",
              keyframes([
                style({ opacity: 0, transform: "translateY(-20%)", offset: 0 }),
                style({
                  opacity: 0.5,
                  transform: "translateY(-5px) scale(1.05)",
                  offset: 0.5,
                }),
                style({
                  opacity: 1,
                  transform: "translateY(0) scale(1)",
                  offset: 1,
                }),
              ])
            ),
          ]),
          { optional: true }
        ),
      ]),
    ]),
  ],
})
export class RoomComponent implements OnInit {
  @Input()
  players: any;
  @Input()
  client: any;
  @Input()
  isAdmin: boolean;
  constructor(private socketService: SocketService) {}
  socket: any;
  start: boolean = false;
  deck: any;
  clientObj: any;
  opponentsObj: any;
  message: any;

  ngOnInit(): void {
    this.socket = this.socketService.getSocket();
    this.socket.emit("getDisconnectedContentRoom", { client: this.client });
    this.socket.on("players", (data: any) => {
      this.players = data.playersDict.map((e) => {
        return e.name;
      });
    });
    this.socket.on("start", (data: any) => {
      this.start = data.start;
      this.opponentsObj = data.playersDict.filter((e) => {
        return e.name != this.client;
      });
      this.clientObj = data.playersDict.filter((e) => {
        return e.name == this.client;
      })[0];
      this.message = data.message;
      this.deck = data.deck;
    });
  }

  removePlayer(player) {
    this.socket.emit("removePlayer", { name: player });
  }

  startGame() {
    if (this.isAdmin) {
      this.socket.emit("startGame", "start", (data) => {
        this.start = data.start;
        this.opponentsObj = data.playersDict.filter((e) => {
          return e.name != this.client;
        });
        this.clientObj = data.playersDict.filter((e) => {
          return e.name == this.client;
        })[0];
        this.message = data.message;
        this.deck = data.deck;
      });
    }
  }
}
